import styled from "styled-components";
import { StyledNavList } from "../../../styles/styledComponents";
import { SiteNavEl } from "./SiteNavEl";
import { SiteLogo } from "./SiteLogo";
import { baseURL } from "../Routes";

const StiledSiteNav = styled.nav`
flex-grow: 2;
`


const homeMenu = [{ linkName: 'Trending', path: `trending` }, { linkName: 'Top-rated', path: `top-rated` },
{ linkName: 'Upcoming', path: `upcoming` }]

export const SiteNav = () => { 
   
    return (
        <StiledSiteNav>
            <StyledNavList site>
                <SiteLogo path={baseURL }></SiteLogo>
                <SiteNavEl  path={baseURL } linkName='Home' menuList={homeMenu}></SiteNavEl>
                <SiteNavEl path={`${baseURL}movies` } linkName='Movies' ></SiteNavEl>
                
            </StyledNavList>
        </StiledSiteNav>)
} 